.todo-app{
  margin: 100px auto;
  max-width: 600px;
}
.task-list{
  font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
  border-collapse: collapse;
  text-align: center;

  &__caption{
    font-size: 32px;
  }
  &__name{

  }
  #tasks-list__description{
    color: #181d42;
    font-size: 14px;
  }
  a{
    text-decoration: none;
    color: #181d42;
    label{
      cursor: pointer;
    }
    &:hover{
      color: darken(#181d42,100%);
    }
  }
  th, td {
    border-style: solid;
    border-width: 0 1px 1px 0;
    border-color: white;
    font-size: 14px;
  }
  th, td:first-child {
    background: #AFCDE7;
    color: white;
    padding: 10px 20px;
    font-size: 18px;
  }
  .tags{
    font-size: 12px;
    color: #8e1822;
    cursor: pointer;
    &:hover{
      text-decoration: underline;
    }
  }
  &__home-button {
    font-size: 18px;
    margin-bottom: 10px;
    a{
      display:inline-block;
      width: 80px;
      height: 20px;
      text-decoration: none;
      color: #1f2766;
    }
  }
}

td {
  background: #D8E6F3;
  .bold{
    font-weight: 800;

  }
}
th:first-child, td:first-child {
  text-align: left;
}

